<template>
  <div class="hello">
    <h2>Test Graphs</h2>
    <div class="wrap">
      <apexchart :type="typeGraph" :options="options" :series="series" class="graphs" ></apexchart>
      <div>
        <div class="input-group mb-3" v-for="(item, index) in series[0].data">
          <input type="text" class="form-control" v-model="series[0].data[index]">
        </div>
      </div>
    </div>
    <div class="btn-group" role="group" aria-label="Basic example">
      <button type="button" class="btn btn-secondary" @click="changeType('bar')">Bar</button>
      <button type="button" class="btn btn-secondary" @click="changeType('line')">Line</button>
      <button type="button" class="btn btn-secondary" @click="changeType('heatmap')">Heatmap</button>
      <button type="button" class="btn btn-secondary" @click="changeType('area')">Area</button>
    </div>
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'
export default {
  name: 'graphs',
  data () {
    return {
        typeGraph: 'bar',
        options: {
          chart: {
            id: 'vuechart-example'
          },
          xaxis: {
            categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998]
          }
        },
        series: [{
          name: '',
          data: [30, 40, 45, 50, 49, 60, 70, 91]
        }]
      }
    },
    methods: {
      changeType(val){
        this.typeGraph = val;
      }
    },
    components: {
      'apexchart': VueApexCharts
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.graphs{
  width: 50%;
}
/*.input-group{
  display: flex;
  flex-direction: column;
}*/
.wrap{
  display: flex;
  justify-content: space-around;
}

</style>


